*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  font-family: system-ui, -apple-system, 'Segoe UI', Roboto, Helvetica, Arial,
    sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji';
}

.home a,
.home a:visited,
.home a:active {
  color: #487ef7;
}

.home a:hover {
  color: #083ea7;
}

.home {
  padding: 0 20px;
}

.week-list {
  line-height: 1.6em;
}
